@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap');
*{
  padding:0;
  margin: 0;
  box-sizing: border-box ;
  font-family:"Rubik", sans-serif;
}
#root{
  min-height: 100vh;
  display: flex;
  animation: fadeIn 3s;
}
@keyframes fadeIn {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 100;
  }
}